import React from 'react';
import {
  Button,
  Card,
  CardActions,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
} from '@material-ui/core';
import { useFoodStateValue } from '../context/food-state';

export const FoodCard = ({ title, imgSrc }) => {
  const { foodState, setFoodState } = useFoodStateValue();

  return (
    <Card className='food-card'>
      <CardActionArea
        onClick={() => {
          setFoodState([...foodState, { food: title, imgSrc }]);
        }}
      >
        <CardMedia
          component='img'
          alt='Contemplative Reptile'
          height='140'
          image={imgSrc}
          title='Contemplative Reptile'
        />
        <CardContent>
          <Typography gutterBottom variant='h6' component='h5'>
            {title}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size='small' color='primary'>
          Info
        </Button>
        <Button size='small' color='primary'>
          Přidat
        </Button>
      </CardActions>
    </Card>
  );
};
